import React from 'react'
import { useLocation } from 'react-router-dom'
import Airtime from '../components/Airtime'
import CableTv from '../components/CableTv'
import DataBundle from '../components/DataBundle'
import Electricity from '../components/Electricity'
import Epin from '../components/Epin'
import Header from '../components/Header'
import Insurance from '../components/Insurance'
import VehiclePaper from '../components/VehiclePaper'

function CategoryPage() {
    const location = useLocation()
    console.log(location.state)

    const switchCategory = ()=> {
        switch(location.state.categoryId) {
            case 'airtime':
                return <Airtime/>
                
            case 'databundle':
                return <DataBundle/>
                
            case 'cabletv':
                return <CableTv/>
                
            case 'electricity':
                return <Electricity/>
                
            case 'epin':
                return <Epin/>
                
            case 'insurance':
                return <Insurance/>
                    
            case 'vehiclepaper':
                return <VehiclePaper/>
                
            default:
                return
                
        }
    }

    return (
        <div>
            <Header />
            {
                location? switchCategory() : null
            }
        </div>
    )
}

export default CategoryPage
