import React, { useState, useEffect } from 'react'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import '../styles/category.css'
import back_btn from '../assets/back_btn.png'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";


const Biller = ({billerName, billerId, billerImage, id, onClick, categoryId})=> (
  <div className='biller_tab' onClick={onClick}>
    <div className='biller_flex'>
      <img src={billerImage} alt='biller' className='biller_image'/>
      <p>{billerName}</p>
    </div>
    <hr/>
  </div>
)
const Product = ({productName, amount, id, onClick})=> (
    <div className='product_tab' onClick={onClick}>
        <p>{productName}</p>
        <hr/>
    </div>
)

function DataBundle() {
    const [billers, setBillers] = useState([])
    const [phoneNumber, setPhoneNumber] = useState('')
    const [pin, setPin] = useState('')
    const [isLoading, setIsLoading] = useState(true)

    const [selectedBiller, setSelectedBiller] = useState({
        billerName: '',
        billerImage: '',
        billerId: '',
    })
    const [selectedProduct, setSelectedProduct] = useState({
        productName: '',
        id: '',
        amount: ''    })
    const [isBillerSelected, setIsBillerSelected] = useState(false)
    const [isProductSelected, setIsProductSelected] = useState(false)
    const [products, setProducts] = useState([])
    
    useEffect(()=> {
        getBillers()
    }, [])

    useEffect(()=> {
        console.log(selectedBiller)
        console.log(billers)
    }, [selectedBiller, billers])
    
    const paymentUrl = 'https://x2rq8qbt0d.execute-api.us-east-1.amazonaws.com/dev/billerPayment'
    const billerUrl = 'https://x2rq8qbt0d.execute-api.us-east-1.amazonaws.com/dev/getBillerCategoryList'
    const auth_token = 'SK-DEV-00001-UNIVACITI-9999999999-1558D1554781C926F2D9A330B020CA86ABA0F241DB7BAD26F88305F3F48D590A66CC73CE6AD2219A005E7640DDE662BA'

    const getBillers = async ()=> {
        await fetch(billerUrl, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                Authorization: auth_token
            },
            body:JSON.stringify({
                "categoryId":"databundle"
            }) 
            }).then(res => res.json())
            .then(json => setBillers(json.result))
            setIsLoading(false)
    }
    useEffect(()=> {
        console.log(products)
    }, [products])

    const getProducts = async (billerId)=> {
        await fetch('https://x2rq8qbt0d.execute-api.us-east-1.amazonaws.com/dev/getBillerProductList', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                Authorization: auth_token
            },
            body:JSON.stringify({
                "billerId":billerId,
                "categoryId":"databundle"
            }) 
            }).then(res => res.json())
            .then(json => setProducts(json.result))
            setIsLoading(false)
    }
    const pay = async ()=> {
        var responseCode, responseMessage
        enter_pin()
        console.log(pin)
        await fetch(paymentUrl, 
          {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              Authorization: auth_token
            },
            body: JSON.stringify({
            "reference": "80981348A83440904042300319",
            "billerCustomerId": phoneNumber,
            "categoryId":"databundle",
            "customField2":"",
            "customField3":"",
            "amount": selectedProduct.amount,
            "itemFee": "0",
            "mobileNumber": "09049957786",
            "billerCustomerName": "ANTHONY M MORAH",
            "source": "openapi",
            "debitAccountName": "ANTHONY M MORAH",
            "debitAccountNumber": "4460518974",
            "billerName": selectedBiller.billerName,
            "paymentItemCode": selectedProduct.id,
            "username": "09049957786",
            "password": pin,
            "saveBeneficiary": "NO",
            "countryCode":"234"
            })
          }
        ).then(res => res.json())
        .then(json => {
          console.log(json.responseMessage)
          responseCode = json.responseCode
          responseMessage = json.responseMessage
        })
        if(responseCode === "00") alert('Purchase successful')
        else alert(responseMessage)
    }

    const enter_pin = ()=> {
        if(phoneNumber!=='') {
          let inputPin = prompt('Enter Pin', '')
          setPin(inputPin)
          return true
        }
        else alert('No valid phonenumber')
    }
  
  return (
      <div className='airtime_recharge'>
        <h2>Data Bundle</h2>
        {isBillerSelected? 
          <>
          {isLoading?
            <>
                <Loader
                    type="BallTriangle"
                    color="#00BFFF"
                    height={100}
                    width={100}
                    timeout={10000} //3 secs
                />
            </>
            :
            <>
            {
                isProductSelected?
                <div className='selected_product'>
                    <img className='back_button' src={back_btn} alt='back' onClick={()=> setIsProductSelected(!isProductSelected)}/>
                    <div className='biller_flex'>
                        <img className='biller_image' src={selectedBiller.billerImage} alt='product'/>
                        <h2>{selectedProduct.productName}</h2>
                    </div>
                    <div style={{display: 'flex', flexDirection: 'column', marginBottom: '3%'}}>
                        <TextField id="outlined-basic" label="Enter Phone Number" variant="standard" value={phoneNumber}
                            onChange={(e)=>setPhoneNumber(e.target.value)} />
                        <TextField id="outlined-basic" label="" variant="standard" value={selectedProduct.amount}/>
                    </div>
                    <Button 
                        variant="contained" 
                        id='pay_btn' size='large'
                        onClick={pay}
                    >
                        Pay
                    </Button>
                </div>
                :
                <>
                    <img className='back_button' src={back_btn} alt='back' 
                    onClick={()=> {
                        setIsBillerSelected(!isBillerSelected)
                    }}/>
                    <div className='biller_flex'>
                        <img className='biller_image' src={selectedBiller.billerImage} alt='biller'/>
                        <h2>{selectedBiller.billerName}</h2>
                    </div>
                    {
                        products.map((p, index)=>(
                            <Product 
                                productName={p.paymentItemName} 
                                id={p.paymentItemId} amount={p.amount} key={index}
                                onClick={()=> {
                                    setSelectedProduct({
                                        productName: p.paymentItemName,
                                        id: p.paymentItemId,
                                        amount: p.amount
                                    })
                                    setIsProductSelected(!isProductSelected)
                                }}
                            />
                        ))
                    }
                </>
            }               
            </>
            }
            
          </>
          :
          <>
            {isLoading?
            <>
            <Loader
                type="BallTriangle"
                color="#00BFFF"
                height={100}
                width={100}
                timeout={10000} //3 secs
            />
            </>
            :
            <>
            {
                billers.map((ab, index)=> (
                    <Biller 
                        billerId={ab.billerId} billerName={ab.billerName} 
                        billerImage={ab.serviceLogo} id={ab.id} key={index}
                        onClick={()=> {
                            setIsLoading(true)
                            getProducts(ab.billerId)
                            setSelectedBiller({
                            billerImage: ab.serviceLogo, billerName: ab.billerName
                            })
                            console.log(selectedBiller)
                            setIsBillerSelected(!isBillerSelected)
                        }}
                    />
                ))
            }
            </>
            }
          </>
        }
      </div>
  )
}

export default DataBundle
