import React, { useState, useEffect } from 'react'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import '../styles/category.css'
import back_btn from '../assets/back_btn.png'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

const Biller = ({billerName, billerId, billerImage, id, onClick})=> (
  <div className='biller_tab' onClick={onClick}>
    <div className='biller_flex'>
      <img src={billerImage} alt='biller' className='biller_image'/>
      <p>{billerName}</p>
    </div>
    <hr/>
  </div>
)

function Electricity() {
  const [billers, setBillers] = useState([])
  const [meterNumber, setMeterNumber] = useState('')
  const [amount, setAmount] = useState('')
  const [pin, setPin] = useState('')
  const [isLoading, setIsLoading] = useState(true)

  const [selectedBiller, setSelectedBiller] = useState({
    billerName: '',
    billerImage: '',
    billerId: ''
  })
  const [isSelected, setIsSelected] = useState(false)
  
  useEffect(()=> {
    getBillers()
  }, [])

  useEffect(()=> {
    console.log(billers)
  }, [billers])

  useEffect(()=> {
    console.log(meterNumber, amount)
  }, [meterNumber, amount])

  const billerUrl = 'https://x2rq8qbt0d.execute-api.us-east-1.amazonaws.com/dev/getBillerCategoryList'
  const paymentUrl = 'https://x2rq8qbt0d.execute-api.us-east-1.amazonaws.com/dev/billerPayment'
  const auth_token = 'SK-DEV-00001-UNIVACITI-9999999999-1558D1554781C926F2D9A330B020CA86ABA0F241DB7BAD26F88305F3F48D590A66CC73CE6AD2219A005E7640DDE662BA'

  const getBillers = async ()=> {
    await fetch(billerUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: auth_token
      },
      body:JSON.stringify({
        "categoryId":"electricity"
      }) 
      }).then(res => res.json())
      .then(json => setBillers(json.result))
      setIsLoading(false)
  }
  const pay = async ()=> {
      var responseCode, responseMessage
      enter_pin()
      console.log(pin)
      await fetch(paymentUrl, 
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: auth_token
          },
          body: JSON.stringify({
          "reference": "80981348A83440904042300319",
          "billerCustomerId": meterNumber,
          "categoryId":"electricity",
          "customField2":"",
          "customField3":"",
          "amount": amount,
          "itemFee": "0",
          "mobileNumber": "09049957786",
          "billerCustomerName": "ANTHONY M MORAH",
          "source": "openapi",
          "debitAccountName": "ANTHONY M MORAH",
          "debitAccountNumber": "4460518974",
          "billerName": selectedBiller.billerName,
          "paymentItemCode": selectedBiller.billerId,
          "username": "09049957786",
          "password": pin,
          "saveBeneficiary": "NO",
          "countryCode":"234"
          })
        }
      ).then(res => res.json())
      .then(json => {
        console.log(json.responseMessage)
        responseCode = json.responseCode
        responseMessage = json.responseMessage
      })
      if(responseCode === "00") alert('Purchase successful')
      else alert(responseMessage)
  }

  const enter_pin = ()=> {
      if(meterNumber!=='' && amount!=='') {
        let inputPin = prompt('Enter Pin', '')
        setPin(inputPin)
        return true
      }
      else alert('No valid meternumber')
  }
  
  return (
      <div className='airtime_recharge'>
        <h2>Electricity</h2>
        {isSelected? 
          <div className='selected_biller'>
            <img className='back_button' src={back_btn} alt='back' onClick={()=> setIsSelected(!isSelected)}/>
            <div className='biller_flex'>
              <img className='biller_image' src={selectedBiller.billerImage} alt='biller'/>
              <h2>{selectedBiller.billerName}</h2>
            </div>
            <div style={{display: 'flex', flexDirection: 'column', marginBottom: '3%'}}>
              <TextField id="outlined-basic" label="Enter Meter Number" variant="standard" value={meterNumber}
                onChange={(e)=>setMeterNumber(e.target.value)} />
              <TextField id="outlined-basic" label="Enter Amount" variant="standard" value={amount}
                onChange={(e)=>setAmount(e.target.value)} />
            </div>
            <Button 
              variant="contained" 
              id='pay_btn' size='large'
              onClick={pay}
            >
                Pay
            </Button>
          </div>
          :
          <>
            {isLoading?
            <>
            <Loader
                type="BallTriangle"
                color="#00BFFF"
                height={100}
                width={100}
                timeout={10000} //3 secs
            />
            </>
            :
            <>
            {
                billers.map((ab, index)=> (
                    <Biller 
                        billerId={ab.billerId} billerName={ab.billerName} 
                        billerImage={ab.serviceLogo} id={ab.id} key={index}
                        onClick={()=> {
                            setSelectedBiller({
                            billerImage: ab.serviceLogo, billerName: ab.billerName
                            })
                            console.log(selectedBiller)
                            setIsSelected(!isSelected)
                        }}
                    />
                ))
            }
            </>
            }
          </>
        }
      </div>
  )
}

export default Electricity
