export const initialState = {
  categoryId: 'deansMsg',
};


const reducer = (state, action) => {
  console.log(action);
  switch (action.type) {
    case "airtime":
      return {...state, categoryId: 'airtime'};

    case "databundle":
      return { ...state, categoryId: 'databundle' };

    case "cabletv":
      return { ...state, categoryId: 'cabletv' };
  
    case "electricity":
      return { ...state, categoryId: 'electricity' };

    case "epin":
      return { ...state, categoryId: 'epin' };

    case "vehiclepaper":
      return { ...state, categoryId: 'vehiclepaper' };
        
    case "insurance":
      return { ...state, categoryId: 'insurance' };
        
    default:
      return state;
  }
};

export default reducer;
