import './App.css';
import LandingPage from './pages/LandingPage';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import CategoryPage from './pages/CategoryPage';

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path='/' exact>
            <LandingPage />
          </Route>
          <Route path='/category/:cat'>
            <CategoryPage />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
