import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import '../styles/categories.css'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

const Category = ({categoryName, categoryId, onClick})=> (
    <Link 
        to={{
            pathname: `/category/:${categoryId}`,
            state: {categoryId: categoryId}
        }}
        className='category_link'
    >
        <div className='category_tab'>
            <p>{categoryName}</p>
            <hr/>
        </div>
    </Link>
)

function Categories() {

    const [isLoading, setIsLoading] = useState(true)
    const auth_token = 'SK-DEV-00001-UNIVACITI-9999999999-1558D1554781C926F2D9A330B020CA86ABA0F241DB7BAD26F88305F3F48D590A66CC73CE6AD2219A005E7640DDE662BA'
    const url = 'https://x2rq8qbt0d.execute-api.us-east-1.amazonaws.com/dev/getCategoryList'

    const [categories, setCategories] = useState([])

    useEffect(()=> {
        getCategories()
    }, [])

    const getCategories = async ()=> {

        await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                Authorization: auth_token
            },
            body:JSON.stringify({
                "request":"categoryList"
            }) 
        }).then(res => res.json())
        .then(json => setCategories(json.result))
        setIsLoading(false)
        
    }
    useEffect(()=> {
        console.log(categories)
    }, [categories])

    return (
        <div className='categories_container'>
            <h2>Categories</h2>
            {isLoading? 
                <>
                    <Loader
                        type="BallTriangle"
                        color="#00BFFF"
                        height={100}
                        width={100}
                        timeout={10000} //3 secs
                    />
                </>
                :
                <>
                    {categories.map((c)=> (
                        <Category 
                            categoryName={c.categoryName} key={c.categoryId} 
                            categoryId={c.categoryId}
                        />
                    ))}
                </>
            }
        </div>
    )
}

export default Categories
